import { Suspense, lazy } from 'react';
// import { Navigate } from 'react-router-dom';
import AuthGuard from './components/AuthGuard';
// import BlogLayout from './components/blog/BlogLayout';
// import BrowseLayout from './components/BrowseLayout';
import DashboardLayout from './components/dashboard/DashboardLayout';
// import DocsLayout from './components/docs/DocsLayout';
import GuestGuard from './components/GuestGuard';
import LoadingScreen from './components/LoadingScreen';
import MainLayout from './components/MainLayout';

const Loadable = (Component) => (props) => (
  <Suspense fallback={<LoadingScreen />}>
    <Component {...props} />
  </Suspense>
);

const Login = Loadable(lazy(() => import('./pages/authentication/Login')));
const PasswordRecovery = Loadable(lazy(() => import('./pages/authentication/PasswordRecovery')));
const PasswordReset = Loadable(lazy(() => import('./pages/authentication/PasswordReset')));
const Register = Loadable(lazy(() => import('./pages/authentication/Register')));
const VerifyCode = Loadable(lazy(() => import('./pages/authentication/VerifyCode')));

// Blog pages

// const BlogPostCreate = Loadable(lazy(() => import('./pages/blog/BlogPostCreate')));
// const BlogPostDetails = Loadable(lazy(() => import('./pages/blog/BlogPostDetails')));
// const BlogPostList = Loadable(lazy(() => import('./pages/blog/BlogPostList')));

// Dashboard pages

const Chat = Loadable(lazy(() => import('./pages/dashboard/Chat')));
const CustomerDetails = Loadable(lazy(() => import('./pages/dashboard/CustomerDetails')));
const CustomerEdit = Loadable(lazy(() => import('./pages/dashboard/CustomerEdit')));
const CustomerList = Loadable(lazy(() => import('./pages/dashboard/CustomerList')));
const Finance = Loadable(lazy(() => import('./pages/dashboard/Finance')));
const InvoiceDetails = Loadable(lazy(() => import('./pages/dashboard/InvoiceDetails')));
const InvoiceList = Loadable(lazy(() => import('./pages/dashboard/InvoiceList')));
const OrderDetails = Loadable(lazy(() => import('./pages/dashboard/OrderDetails')));
const OrderList2 = Loadable(lazy(() => import('./pages/dashboard/OrderList')));
const Overview = Loadable(lazy(() => import('./pages/dashboard/Overview')));
const ProductCreate = Loadable(lazy(() => import('./pages/dashboard/ProductCreate')));
const ProductList = Loadable(lazy(() => import('./pages/dashboard/ProductList')));

const AuthorizationRequired = Loadable(lazy(() => import('./pages/AuthorizationRequired')));
const NotFound = Loadable(lazy(() => import('./pages/NotFound')));
const ServerError = Loadable(lazy(() => import('./pages/ServerError')));

const RatingPage = Loadable(lazy(() => import('./pages/rating/RatingPage')));

// const ClientsList = Loadable(lazy(() => import('./views/dashboard/workshop/clients/ClientsList')));
const ClientCreate = Loadable(lazy(() => import('./views/dashboard/workshop/clients/ClientCreate')));
// const ClientDetails = Loadable(lazy(() => import('./views/dashboard/workshop/clients/ClientDetails')));

const DriverList = Loadable(lazy(() => import('./views/dashboard/drivers/DriverList')));
const DriverDetail = Loadable(lazy(() => import('./views/dashboard/drivers/DriverDetail')));

const VehiclesWorkshopList = Loadable(lazy(() => import('./views/dashboard/workshop/vehicles/VehiclesWorkshopList')));
const VehicleWorkshopCreate = Loadable(lazy(() => import('./views/dashboard/workshop/vehicles/VehicleWorkshopCreate')));
const VehicleWorkshopDetails = Loadable(lazy(() => import('./views/dashboard/workshop/vehicles/VehicleWorkshopDetails')));

const OrdersList = Loadable(lazy(() => import('./views/dashboard/workshop/orders/OrdersList')));
const OrdersCreate = Loadable(lazy(() => import('./views/dashboard/workshop/orders/OrdersCreate')));
const OrderWorkshopDetails = Loadable(lazy(() => import('./views/dashboard/workshop/orders/OrderDetails')));
const InactiveOrderDetails = Loadable(lazy(() => import('./views/dashboard/workshop/orders/InactiveOrderDetails')));
const AppointmentsWorshopList = Loadable(lazy(() => import('./views/dashboard/workshop/appointments/AppointmentsList')));
const AppointmentsWorshopDetails = Loadable(lazy(() => import('./views/dashboard/workshop/appointments/AppointmentsDetails')));
// const AppointmentsListAlliances = Loadable(lazy(() => import('./views/dashboard/alliances/appointmets/AppointmentsListAlliances')));
// const AppointmentsDetailsAlliances = Loadable(lazy(() => import('./views/dashboard/alliances/appointmets/AppointmentsDetailsAlliances')));

const AdminList = Loadable(lazy(() => import('./views/dashboard/workshop/admins/AdminList')));
const AdminCreate = Loadable(lazy(() => import('./views/dashboard/workshop/admins/AdminCreate')));
// vistas de tiendas
const OrderCreate = Loadable(lazy(() => import('./views/dashboard/store/orders/OrderCreate')));
const OrderList = Loadable(lazy(() => import('./views/dashboard/store/orders/OrderList')));
const ClientsList = Loadable(lazy(() => import('./views/dashboard/store/clients/ClientsList')));
const OrderDetail = Loadable(lazy(() => import('./views/dashboard/store/orders/OrderDetail')));

const routes = [
  {
    path: 'authentication',
    children: [
      {
        path: 'login',
        element: (
          <GuestGuard>
            <Login />
          </GuestGuard>
        )
      },
      {
        path: 'login-unguarded',
        element: <Login />
      },
      {
        path: 'password-recovery',
        element: <PasswordRecovery />
      },
      {
        path: 'password-reset',
        element: <PasswordReset />
      },
      {
        path: 'register',
        element: (
          <GuestGuard>
            <Register />
          </GuestGuard>
        )
      },
      {
        path: 'register-unguarded',
        element: <Register />
      },
      {
        path: 'verify-code',
        element: <VerifyCode />
      }
    ]
  },
  {
    path: 'dashboard',
    element: (
      <AuthGuard>
        <DashboardLayout />
      </AuthGuard>
    ),
    children: [
      {
        index: true,
        element: <Overview />
      },
      {
        path: 'store',
        children: [
          {
            index: true,
            element: <OrderCreate />
          },
          {
            path: 'order/create',
            element: <OrderCreate />
          },
          {
            path: 'orders',
            element: <OrderList />
          },
          {
            path: 'order/detail/:id',
            element: <OrderDetail />
          },
          {
            path: 'clients/register',
            element: <OrderCreate />
          },
          {
            path: 'clients',
            element: <ClientsList />
          },
          // {
          //   path: 'clients/detail/:id',
          //   element: <AppointmentsDetailsAlliances />
          // }
        ]
      },
      {
        path: 'drivers',
        children: [
          {
            index: true,
            element: <DriverList />
          },
          {
            path: 'crear',
            element: <ClientCreate />
          },
          {
            path: 'detail/:id',
            element: <DriverDetail />
          },
          {
            path: 'vehiculos',
            element: <VehiclesWorkshopList />
          },
          {
            path: 'vehiculos/crear',
            element: <VehicleWorkshopCreate />
          },
          {
            path: 'vehiculos/detalles/:id',
            element: <VehicleWorkshopDetails />
          },
          {
            path: 'ordenes',
            element: <OrdersList />
          },
          {
            path: 'ordenes/crear',
            element: <OrdersCreate />
          },
          {
            path: 'ordenes/detalles/:id',
            element: <OrderWorkshopDetails />
          },
          {
            path: 'ordenes/inactivas/detalles/:id',
            element: <InactiveOrderDetails />
          },
          {
            path: 'citas',
            element: <AppointmentsWorshopList />
          },
          {
            path: 'citas/detalles/:id',
            element: <AppointmentsWorshopDetails />
          }
        ]
      },
      {
        path: 'admins',
        children: [
          {
            path: 'listado',
            element: <AdminList />
          },
          {
            path: 'crear',
            element: <AdminCreate />
          }
        ]
      },
      {
        path: 'chat',
        children: [
          {
            index: true,
            element: <Chat />
          },
          {
            path: 'new',
            element: <Chat />
          },
          {
            path: ':threadKey',
            element: <Chat />
          }
        ]
      },
      {
        path: 'customers',
        children: [
          {
            index: true,
            element: <CustomerList />
          },
          {
            path: ':customerId',
            element: <CustomerDetails />
          },
          {
            path: ':customerId/edit',
            element: <CustomerEdit />
          }
        ]
      },
      {
        path: 'invoices',
        children: [
          {
            index: true,
            element: <InvoiceList />
          },
          {
            path: ':invoiceId',
            element: <InvoiceDetails />
          }
        ]
      },
      {
        path: 'orders',
        children: [
          {
            index: true,
            element: <OrderList2 />
          },
          {
            path: ':orderId',
            element: <OrderDetails />
          }
        ]
      },
      {
        path: 'finance',
        element: <Finance />
      },
      {
        path: 'products',
        children: [
          {
            index: true,
            element: <ProductList />
          },
          {
            path: 'new',
            element: <ProductCreate />
          }
        ]
      },
    ]
  },
  {
    path: '*',
    element: <MainLayout />,
    children: [
      {
        index: true,
        element:
          (
            <GuestGuard>
              <Login />
            </GuestGuard>
          )
      },
      {
        path: '401',
        element: <AuthorizationRequired />
      },
      {
        path: '404',
        element: <NotFound />
      },
      {
        path: '500',
        element: <ServerError />
      },
      {
        path: '*',
        element: <NotFound />
      }
    ]
  },
  {
    path: 'encuesta',
    element: <MainLayout />,
    children: [
      {
        index: true,
        element: <RatingPage />
      }
    ]
  }
];

export default routes;
