const DashboardSidebar = (props) => {
  const { onMobileClose, openMobile } = props;
  const location = useLocation();
  // const { user } = useAuth();
  const lgUp = useMediaQuery((theme) => theme.breakpoints.up('lg'));

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    sections = [];
    const rank = localStorage.getItem('rank_id');
    if (rank === 'ADM') {
      sections.push({
        title: 'General',
        items: [
          {
            title: 'Resumen',
            path: '/dashboard',
            icon: <ChartSquareBarIcon fontSize="small" />
          },
        ]
      },
      // {
      //   title: 'Administración',
      //   items: [
      //     {
      //       title: 'Ver Admins',
      //       path: '/dashboard/admins/listado',
      //       icon: <UsersIcon fontSize="small" />
      //     },
      //     {
      //       title: 'Nuevo Admin',
      //       path: '/dashboard/admins/crear',
      //       icon: <UsersIcon fontSize="small" />
      //     },
      //   ]
      // },
      {
        title: 'Tiendas',
        items: [
          {
            title: 'Resumen',
            path: '/dashboard/tiendas',
            icon: <EventNote fontSize="small" />
          },
          {
            title: 'Registrar Tienda',
            path: '/dashboard/tiendas/registro',
            icon: <BriefcaseIcon fontSize="small" />
          },
        ]
      },
      {
        title: 'Drivers',
        items: [
          // {
          //   title: 'Registrar',
          //   path: '/dashboard/drivers/crear',
          //   icon: <BriefcaseIcon fontSize="small" />
          // },
          {
            title: 'Ver Drivers',
            path: '/dashboard/drivers',
            icon: <CarRental fontSize="small" />,
          },
        ]
      });
    } else if (rank === 'STO') {
      sections.push({
        title: 'General',
        items: [
          {
            title: 'Resumen',
            path: '/dashboard',
            icon: <ChartSquareBarIcon fontSize="small" />
          },
        ]
      },
      {
        title: 'Ordenes',
        items: [
          {
            title: 'Resumen',
            path: '/dashboard/store/orders',
            icon: <EventNote fontSize="small" />
          },
          {
            title: 'Crear Orden',
            path: '/dashboard/store/order/create',
            icon: <BriefcaseIcon fontSize="small" />
          },
        ]
      },
      {
        title: 'Clientes',
        items: [
          {
            title: 'Resumen',
            path: '/dashboard/store/clients',
            icon: <EventNote fontSize="small" />
          },
          // {
          //   title: 'Registrar un cliente',
          //   path: '/dashboard/store/clients/register',
          //   icon: <BriefcaseIcon fontSize="small" />
          // },
        ]
      },
      {
        title: 'Chat',
        items: [
          {
            title: 'Chat',
            path: '/dashboard/chat',
            icon: <ChatAltIcon fontSize="small" />
          },
        ]
      },);
    }
  }, [location.pathname]);

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Scrollbar options={{ suppressScrollX: true }}>
        {openMobile && (
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'center',
              mt: 6
            }}
          >
            <img
              src={logo}
              alt="logo"
              style={{ width: '35%' }}
            />
          </Box>
        )}
        <Box sx={{ p: 2 }}>
          <Box
            sx={{
              alignItems: 'center',
              backgroundColor: 'background.default',
              borderRadius: 1,
              display: 'flex',
              overflow: 'hidden',
              p: 2
            }}
          >
            <Avatar
              sx={{
                height: 42,
                width: 42,
                backgroundColor: '#00CCFF'
              }}
            >
              {getInitials(localStorage.getItem('name'))}
            </Avatar>
            <Box sx={{ ml: 2 }}>
              <Typography
                color="textPrimary"
                variant="subtitle2"
              >
                {localStorage.getItem('name')}
              </Typography>
              <Typography
                color="textSecondary"
                variant="body2"
              >
                Rango:
                {' '}
                <Link
                  color="primary"
                >
                  {localStorage.getItem('rank_value')}
                </Link>
              </Typography>
            </Box>
          </Box>
        </Box>
        <Divider />
        <Box sx={{ p: 2 }}>
          {sections.map((section) => (
            <NavSection
              key={section.title}
              pathname={location.pathname}
              sx={{
                '& + &': {
                  mt: 3
                }
              }}
              {...section}
            />
          ))}
        </Box>
        {/* <Divider />
        <Box sx={{ p: 2 }}>
          <Typography
            color="textPrimary"
            variant="subtitle2"
          >
            Need Help?
          </Typography>
          <Typography
            color="textSecondary"
            variant="body2"
          >
            Check our docs
          </Typography>
          <Button
            color="primary"
            component={RouterLink}
            fullWidth
            sx={{ mt: 2 }}
            to="/docs"
            variant="contained"
          >
            Documentation
          </Button>
        </Box> */}
      </Scrollbar>
    </Box>
  );

  if (lgUp) {
    return (
      <Drawer
        anchor="left"
        open
        PaperProps={{
          sx: {
            backgroundColor: 'background.paper',
            height: 'calc(100% - 64px) !important',
            top: '64px !Important',
            width: 280
          }
        }}
        variant="permanent"
      >
        {content}
      </Drawer>
    );
  }

  return (
    <Drawer
      anchor="left"
      onClose={onMobileClose}
      open={openMobile}
      PaperProps={{
        sx: {
          backgroundColor: 'background.paper',
          width: 280
        }
      }}
      variant="temporary"
    >
      {content}
    </Drawer>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

export default DashboardSidebar;
