export class EndpointConfig {
  // URL_DEV = 'https://mac-back-delivery.herokuapp.com/';
  URL_DEV = 'https://mac-back-delivery.herokuapp.com/';
  // URL_DEV = 'http://bloomcker.io:23003/api';

  URL_SOCKET = 'http://localhost:3004';

  headerToken() {
    const token = window.localStorage.getItem('accessToken');
    const headersList = {
      Accept: '*/*',
      Authorization: `Bearer ${token}`,
    };
    return headersList;
  }
}

