import { createContext, useEffect, useReducer } from 'react';
import PropTypes from 'prop-types';
import AuthRepository from '../repositories/auth.repository';

const initialState = {
  isAuthenticated: false,
  isInitialized: false,
  user: {
    id: '',
    avatar: '',
    email: '',
    name: '',
    lastname: '',
    username: '',
    rank_id: '',
    rank_value: ''
  }
};

const handlers = {
  INITIALIZE: (state, action) => {
    const { isAuthenticated, user } = action.payload;

    return {
      ...state,
      isAuthenticated,
      isInitialized: true,
      user
    };
  },
  LOGIN: (state, action) => {
    const { user } = action.payload;

    return {
      ...state,
      isAuthenticated: true,
      user
    };
  },
  LOGOUT: (state) => ({
    ...state,
    isAuthenticated: false,
    user: null
  }),
  REGISTER: (state, action) => {
    const { user } = action.payload;

    return {
      ...state,
      isAuthenticated: true,
      user
    };
  }
};

const reducer = (state, action) => (handlers[action.type]
  ? handlers[action.type](state, action)
  : state);

const AuthContext = createContext({
  ...initialState,
  platform: 'JWT',
  login: () => Promise.resolve(),
  logout: () => Promise.resolve(),
  register: () => Promise.resolve()
});

export const AuthProvider = (props) => {
  const authRepository = new AuthRepository();
  const { children } = props;
  const [state, dispatch] = useReducer(reducer, initialState);

  useEffect(() => {
    const initialize = async () => {
      try {
        const accessToken = window.localStorage.getItem('accessToken');

        if (accessToken) {
          const userDataMe = await authRepository.me();
          const user = {
            id: window.localStorage.getItem('id'),
            avatar: '',
            email: userDataMe.data.email,
            name: userDataMe.data.name,
            lastname: userDataMe.data.lastname,
            username: userDataMe.data.username,
            rank_id: userDataMe.data.role_id,
            rank_value: userDataMe.data.role_id
          };
          dispatch({
            type: 'INITIALIZE',
            payload: {
              isAuthenticated: true,
              user
            }
          });
        } else {
          dispatch({
            type: 'INITIALIZE',
            payload: {
              isAuthenticated: false,
              user: null
            }
          });
        }
      } catch (err) {
        console.error(err);
        dispatch({
          type: 'INITIALIZE',
          payload: {
            isAuthenticated: false,
            user: null
          }
        });
      }
    };

    initialize();
  }, []);

  const login = async (username, password) => {
    const userData = await authRepository.login({ username, password });
    console.log(userData);
    const user = {
      id: userData.username,
      avatar: '',
      email: userData.email,
      name: userData.name,
      lastname: userData.lastname,
      username: userData.username,
      rank_id: userData.role_id,
      rank_value: userData.role_id
    };
    localStorage.setItem('accessToken', userData.access_token);
    localStorage.setItem('name', userData.name);
    localStorage.setItem('id', userData.id);
    // localStorage.setItem('avatar', '../assets/images/Mac/logo.jpg');
    localStorage.setItem('rank_id', userData.role_id);
    localStorage.setItem('rank_value', userData.role_id);

    dispatch({
      type: 'LOGIN',
      payload: {
        user
      }
    });
  };

  const logout = async () => {
    localStorage.removeItem('accessToken');
    localStorage.removeItem('id');
    localStorage.removeItem('name');
    localStorage.removeItem('rank_id');
    localStorage.removeItem('rank_value');
    dispatch({ type: 'LOGOUT' });
  };

  const register = async (body) => {
    const userRegister = await authRepository.register(body);
    console.log('USER REGISTER', userRegister);
    const user = await authRepository.me();
    console.log('USER ME', user);
    localStorage.setItem('accessToken', user.accessToken);

    dispatch({
      type: 'REGISTER',
      payload: {
        user
      }
    });
  };

  return (
    <AuthContext.Provider
      value={{
        ...state,
        platform: 'JWT',
        login,
        logout,
        register
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

AuthProvider.propTypes = {
  children: PropTypes.node.isRequired
};

export default AuthContext;
